import Vue from 'vue'
import Router from 'vue-router'
import Canvas from '../pages/canvas.vue'
import FileForm from '../pages/fileform.vue'
const Child = () => import('../pages/child.vue')//
Vue.use(Router)

const thisRouter = new Router({
    routes: [
        {
            path: '/', //
            component: r => require.ensure(//import
                [],
                () => r(require('../pages/articleList.vue')),
                'articleList'
            )
        },
        {
            path: '/articleList', //
            component: r => require.ensure(
                [],
                () => r(require('../pages/articleList.vue')),
                'articleList'
            )
            /*resolve => require(['../pages/articleList.vue'], resolve)*/
        },
        {
            path: '/manAccount', //
            component: r => require.ensure(
                [],
                () => r(require('../pages/manAccount.vue')),
                'manAccount'
            )
        },
        {
            path: '/canvas',//canvas
            component: Canvas

        },
        {
            path: '/websocket',//
            component: r => require.ensure(//import
                [],
                () => r(require('../pages/webSocket.vue')),
                'websocket'
            )

        },
        {
            path: '/fileForm',//
            component: FileForm

        },
        {
            path: '/ES6',//ES6
            component: r => require.ensure(//import
                [],
                () => r(require('../pages/ES6.vue')),
                'es6'
            )


        },
        {
            path: '/zhengxin',
            component: r => require.ensure(//import
                [],
                () => r(require('../pages/zhengxin.vue')),
                'zhengxin'
            )

        },
        {
            path: '/routerexec:id',
            children: [
                {
                    path: 'child1:childid',
                    components: {
                        default: Child,
                        a: Child,
                        b: Child
                    }
                },
                {
                    path: 'child2:childid',
                    component: Child
                },
                {
                    path: 'child3:childid',
                    component: Child
                }

            ],
            component: r => require.ensure(//import
                [],
                () => r(require('../pages/routerexec.vue')),
                'routerexec'
            )


        },
        {
            path: '*', //
            redirect: '/'
        }
    ]
})

thisRouter.beforeEach((to, from, next) => {
    console.log(to)
    console.log(from)
    console.log(next)
    next()
})

export {thisRouter as default}


